import { graphql, Link } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React from "react";
import Layout from "../components/Layout";
import styled from "styled-components";
import media from "styled-media-query";

const BlogTemplateWrapper = styled.div`
  .arrow {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
  }

  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }
`;

const BlogTitle = styled.span`
  margin: 0;
  min-width: 0;
  line-height: 1.25;
  font-weight: 700;
  margin-bottom: 0.25rem;
  font-size: 3rem;
  margin-top: 0.5rem;
  ${media.lessThan("small")`
    font-size: 2.5rem;
  `}
`;

const BlogMeta = styled.div`
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 28px;
  text-decoration: none;
  margin: 16px 0 40px;
`;

const BlogTime = styled.time``;

const BlogTags = ({ tags }) => (
  <>
    {tags.map((tag, index) => {
      const comma = index === 0 ? "" : ",";
      return (
        <span key={index}>
          {comma}
          <BlogCategoryLink to={`/tags/${tag}`}> {tag}</BlogCategoryLink>
        </span>
      );
    })}
  </>
);

const BlogCategoryLink = styled(Link)`
  text-decoration: none;
  color: #000;
  &:hover {
    color: gray;
  }
`;

const BlogBody = styled.span`
  margin: 20px 0px;
  font-size: 1rem;
  letter-spacing: -0.003em;
  line-height: 1.625;
`;

const BlogNavigation = styled.div`
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #ccc;
`;

const BlogLink = styled(Link)`
  color: #000;
  text-decoration: none;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  cursor: pointer;
  display: flex;
  align-items: center;
  &:hover {
    color: gray;
  }
`;

// eslint-disable-next-line react/display-name
export default ({ data, pageContext, location }) => {
  const { frontmatter, body } = data.mdx;
  const { previous, next } = pageContext;
  const path = location && location.pathname && location.pathname.split("/")[1];
  console.log(data.mdx);
  return (
    <Layout path={path}>
      <BlogTemplateWrapper>
        <BlogTitle>{frontmatter.title}</BlogTitle>
        <BlogMeta>
          <BlogTime>{frontmatter.date}</BlogTime>__
          {frontmatter.tags && <BlogTags tags={frontmatter.tags} />}
        </BlogMeta>
        <BlogBody>
          <MDXRenderer>{body}</MDXRenderer>
        </BlogBody>
        <BlogNavigation>
          {next === false ? (
            <div />
          ) : (
            <>
              {next ? (
                <BlogLink to={next.fields.slug}>
                  <i className="arrow left"></i> <p>{next.frontmatter.title}</p>
                </BlogLink>
              ) : (
                <div />
              )}
            </>
          )}{" "}
          {previous === false ? (
            <div />
          ) : (
            <>
              {previous ? (
                <BlogLink to={previous.fields.slug}>
                  <p>{previous.frontmatter.title}</p>{" "}
                  <i className="arrow right"></i>
                </BlogLink>
              ) : (
                <div />
              )}
            </>
          )}
        </BlogNavigation>
      </BlogTemplateWrapper>
    </Layout>
  );
};

export const query = graphql`
  query PostsBySlug($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      frontmatter {
        title
        date(formatString: "YYYY MMMM Do")
        tags
      }
    }
  }
`;
